export class FeatureAccess {
    users: Number = 0;
    visits: Number = 0;
    leads: Number = 0;
    calculators: Number = 0;
    embedding: { active: Boolean } = { active: false };

    lead_generation: { active: Boolean } = {
        active: false
    };

    analytics: { active: Boolean, user_details: Boolean, traffic_details: Boolean, spreadsheet: Boolean }
    = {
        active: false,
        user_details: false,
        traffic_details: false,
        spreadsheet: false
    };

    cta: { active: Boolean, redirect_url: Boolean, like_follow: Boolean, shares: Boolean }
    = {
        active: false,
        redirect_url: false,
        like_follow: false,
        shares: false
    };

    custom_styling: {
        active: Boolean, background_image: Boolean, custom_tints: Boolean, predefined_color_themes: Boolean,
        custom_themes: Boolean, fonts: Boolean
    }
    = {
        active: false,
        background_image: false,
        custom_tints: false,
        predefined_color_themes: false,
        custom_themes: false,
        fonts: false
    };
    integrations: { active: Boolean, marketo: Boolean, salesforce: Boolean, mailchimp: Boolean, zapier: Boolean, active_campaign: Boolean, get_response: Boolean,aweber :Boolean}
    = {
        active: false,
        marketo: false,
        salesforce: false,
        mailchimp: false,
        zapier: false,
        active_campaign: false,
        get_response: false,
        aweber : false
    };

    custom_branding: { active: Boolean, logo_poweredby: Boolean, share_text: Boolean, cta_build_similar_calc: Boolean }
                         = { 
                             active: false,
                             logo_poweredby: false,
                             share_text: false,
                             cta_build_similar_calc: false
                           };

    priority_support: { active: Boolean } = { active: false };

    account_manager: { active: Boolean } = { active: false };

    api: { active: Boolean } = { active: false };

    training: { active: Boolean } = { active: false };

    confirmation_emails: { active: Boolean, to_self: Boolean, to_user: Boolean }
    = {
        active: false,
        to_self: false,
        to_user: false
    };

    conditional_messaging: { active: Boolean } = { active: false };
    formula_operators: { active: Boolean, simple_operators: Boolean, all_operators: Boolean }
    = {
        active: false,
        simple_operators: false,
        all_operators: false
    };
    templates: { active: Boolean, one_page_card: Boolean, sound_cloud: Boolean, inline_temp: Boolean }
    = {
        active: true,
        one_page_card : false,
        sound_cloud: false,
        inline_temp: false
    };

    variable_cta: { active: Boolean } = { active: false };
    disclaimers: { active: Boolean } = { active: false };
    real_time_results: { active: Boolean } = { active: true };

    constructor(features: any) {
        if (features) {
            for (let keys in this) {
                if (features[keys]) {
                    if(typeof this[keys] === 'number'){
                        this[keys] = features[keys];
                    }
                    for (let sub in this[keys]) {
                        if (features[keys][sub]) {
                            this[keys][sub] = features[keys][sub];
                        } else {
                            //console.log(sub, "elseeee sub_keyss");
                        }
                    }
                } else {
                    //console.log(keys, "elseee keyssss");
                }
            }
            //console.log(this, '***************************',features);
        }
    }


}



